import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { MainComponent } from './main/main.component';
import { InvoiceReceiptUIComponent } from './invoice-receipt-ui/invoice-receipt-ui.component';

const routes: Routes = [
  { 
    path: '',
    component : MainComponent,
    children : [
      { 
        path: '', 
        redirectTo: 'invoice_generate',
        pathMatch: 'full'
      },
      { 
        path: "invoice_generate",
        component: InvoiceReceiptUIComponent
      },
     
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class SettingModuleRoutingModule { }
